.gallery__column--reverse .gallery__card {
  cursor: pointer;
  max-width: 100%;
  position: relative;
  min-height: 250px;
}
.gallery__column--reverse .gallery__card img {
  position: absolute;
  inset: 0;
  opacity: 1;
  transition: 0.3s ease-in-out;
  width: 100%;
  height: 100%;
  min-height: 250px;
  object-fit: cover;
}
.gallery__column--reverse .gallery__card img:hover {
  opacity: 0.2;
}
